import React, { Component } from 'react';
import { login } from '../../../rest/Rest';

export default class Sales extends Component {
  
  componentDidMount() {
    var FormData = require('form-data');
    var form = new FormData();
    form.append('login_email_mob', '9898989898');
    form.append('login_password', 'Test1234');
    //form.set('login_email_mob', '9898989898');
    //form.set('login_password', 'Test1234');
    
    // axios.post('http://localhost/ajpatel/api/login', {
    //   data: form,
    //   headers: {'Access-Control-Allow-Origin': '*' }
    // }).then(function(response) {
    //   console.log(response)
    // });
    login(form)
      .then(function(response) {
        console.log(response)
        return response.text();
      }).then(function(response) {
        console.log(response)
      }).catch(function(ex) {
        console.log('parsing failed', ex)
      })
  }
  
  render() {
    
    return (
      <div>
      Sales
      </div>
    );
  }
}


