import 'whatwg-fetch';

const prefix = 'http://localhost/ajpatelTest/api/';

export function login(formData) {
    return fetch( prefix + 'login', {
        headers: {
            'Access-Control-Allow-Origin': '*',
        },
        method: "POST",
        body: formData
      })
}


